import {PhoneNumber} from './PhoneNumber';
export  class User {
    Email:string
    Name:string
    LastName:string
    Password:string
    Phones:PhoneNumber[]
    _id:String
    constructor(Email:string,Password:string,_id:string="",Name:string="",LastName:string="",PhoneDescription:String="",Phone:String=""){
     
        this.Name=Name
        this.LastName=LastName
        this.Password=Password
        this._id=_id
        this.Phones=[]
        this.Email=Email
        if(PhoneDescription.length>0&&Phone.length>0){
            this.Phones.push(new PhoneNumber(PhoneDescription,Phone))
        }

    }
    

    AddPhone(PhoneDescription:String="",Phone:String=""):void{
        this.Phones.push(new PhoneNumber(PhoneDescription,Phone))
    }

    GetPhones():PhoneNumber[]{
        return this.Phones
    }

}
export default User